<template>
	<div>
		<headerNav title="地址列表"/>
		<van-address-list
				class="hideSelect"
				:list="address"
				@add="onAdd"
				@edit="onEdit"
				@select="onSelect"
		/>
	</div>
</template>

<script>
	import {AddressList} from 'vant';

	import headerNav from '../../../components/header/nav'
	import {mapActions, mapState} from 'vuex';

	export default {
		name: 'AddressList',

		components: {
			headerNav,
			AddressList,
		},

		data() {
			return {}
		},

		computed: {
			...mapState(['address'])
		},

		created() {
			this.loadAddress();
		},

		methods: {
			...mapActions(['loadAddress']),

			onAdd() {
				this.$router.push('/addressDetail')
			},

			onEdit(item, index) {
				this.$router.push('/addressDetail?index=' + index);
			},

			onSelect(item, index) {
				this.$router.push('/addressDetail?index=' + index);
			},
		},
	}
</script>

<style lang="less" scoped>
	.hideSelect {
		.van-radio__icon {
			display: none;
		}
	}
</style>



